import React, { Component } from 'react';

export default class SideBar extends Component {
    sendBackData = () => {
        this.props.parentCallback("Home");
    }
    sendList = () => {
        this.props.parentCallback("List");
    }
    sendGraph = () => {
        this.props.parentCallback("Graph");
    }

    render() {
        return (
            <aside className="main-sidebar skin-blue sidebar-mini">
                <section className="sidebar">
                    <div className="user-panel">
                        {/* <div className="pull-left image">
                            <img src="data:image/jpeg;base64," className="img-circle" alt="User Image" />
                        </div> */}
                        <p>Gulf Booking</p>
                        <a href="#"><i className="fa fa-circle text-success"></i> Online</a>
                    </div>
                    <form action="#" method="get" className="sidebar-form">
                        <div className="input-group">
                            <input type="text" name="q" className="form-control" placeholder="Search..." />
                            <span className="input-group-btn">
                                <button type="submit" name="search" id="search-btn" className="btn btn-flat"><i className="fa fa-search"></i>
                                </button>
                            </span>
                        </div>
                    </form>
                    <ul className="sidebar-menu" data-widget="tree">
                        <li className="header">MAIN NAVIGATION</li>
                        <li className="treeview" onClick={this.sendBackData}>
                            <a  >
                                <i className="fa fa-home"></i>
                                <span>Home</span>

                            </a>
                        </li>
                        <li onClick={this.sendList}>
                            <a >
                                <i className="fa fa-line-chart"></i> <span>List</span>
                            </a>
                        </li>
                        <li onClick={this.sendGraph}>
                            <a>
                                <i className="fa fa-line-chart"></i> <span>Graph</span>
                            </a>
                        </li>
                        <li>
                            <a >
                                <i className="fa fa-heart"></i> <span>Favourite</span>
                            </a>
                        </li>
                        <li>
                            <a >
                                <i className="fa fa-cog"></i> <span>Setting</span>
                            </a>
                        </li>
                    </ul>
                </section>
            </aside>
        )
    }
}